<template>
  <v-container>
    <v-row justify="center">
      <v-col sm="6">
        <v-alert class="subtitle-2" color="cyan" border="left" outlined dense icon="mdi-twitter">
          메뉴 아이템 #4 - 데이터 가져오기
        </v-alert>
      </v-col>
      <v-col sm="6">
        <v-btn @click="fetchData" text color="red">새로고침</v-btn>
      </v-col>
    </v-row>
    <v-row justify="center">
      <v-col sm="12">

        <v-data-table dense :headers="headers" :items="items" item-key="id"
          class="elevation-1 subtitle-1 blue-grey--text">
        </v-data-table>

      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    items: [],
    headers: [
      {
        text: 'ID',
        align: 'start',
        sortable: false,
        filterable: false,
        value: 'id',
      },
      { text: '아이디', value: 'username' },
      { text: '웹사이트', value: 'website' },
      { text: '이메일', value: 'email' },
      { text: '이름', value: 'name' },
      { text: '전화번호', value: 'phone' },
    ],

  }),
  created() {
    this.fetchData()

  },
  methods: {
    fetchData: function () {
      var vm = this;
      this.$axios.get('https://jsonplaceholder.typicode.com/users/')
        .then(function (response) {
          vm.items = response.data 
        })
        .catch(function (error) {
          console.log(error);
        });
    }
  }
}
</script>

